@font-face {
  font-family: "OperatorMono-Book";
  src: url("/fonts/Operator/OperatorMono-Book_Web.woff2") format("woff2"),
    url("/fonts/Operator/OperatorMono-Book_Web.woff2") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "OperatorMono-BookItalic";
  src: url("/fonts/Operator/OperatorMono-BookItalic_Web.woff2") format("woff2"),
    url("/fonts/Operator/OperatorMono-BookItalic_Web.woff2") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "OperatorMono-Bold";
  src: url("/fonts/Operator/OperatorMono-Bold_Web.woff2") format("woff2"),
    url("/fonts/Operator/OperatorMono-Bold_Web.woff2") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "OperatorMono-BoldItalic";
  src: url("/fonts/Operator/OperatorMono-BoldItalic_Web.woff2") format("woff2"),
    url("/fonts/Operator/OperatorMono-BoldItalic_Web.woff2") format("woff");
  font-weight: normal;
  font-style: normal;
}

html {
  font-size: 10px;
}

body {
  font-family: "OperatorMono-Book", -apple-system, BlinkMacSystemFont,
    "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue",
    sans-serif;
  font-size: 1.5rem;
  color: $black;
  font-weight: 400;
}

h1 {
  font-size: 4rem;
  font-family: "OperatorMono-Bold";
  font-weight: 600;
  letter-spacing: -1px;
}
h2 {
  font-size: 3rem;
  font-family: "OperatorMono-Bold";
  font-weight: 500;
  letter-spacing: -1px;
}

h3 {
  font-size: 2.5rem;
  font-family: "OperatorMono-Bold";
  letter-spacing: -1px;
}

h4 {
  font-size: 2rem;
  letter-spacing: -1px;
}

h5 {
  font-size: 1.8rem;
}

h6 {
  font-size: 1.6rem;
}

a {
  text-decoration: none;
  color: $black;
}

p a {
  border-bottom: 2px solid $yellow;
}

p {
  line-height: 2;
}

.title {
  word-wrap: break-word;
  position: relative;
  z-index: 2;
  margin: 1rem;
  line-height: 1.1;
  &--long {
    font-size: 30px;
  }
  &:before {
    content: "";
    width: 50px;
    left: 0;
    top: 0;
    height: 100%;
    top: 10%;
    display: block;
    position: absolute;
    z-index: -1;
    transform: skew(-5deg);
  }
  line-height: 1.1;
  a {
    border-bottom: 0;
    $whitewa: rgba($white, 0.8);
    background-image: linear-gradient(to right, $whitewa 100%, $whitewa 50%);
  }
}

.title--single {
  max-width: 900px;
  font-size: 5rem;
  text-align: left;
}

.highlightShort {
  max-width: 570px;
  padding: 1rem;
  letter-spacing: -1px;
  background: url("/images/photos/blackgrit.png");
  background-color: #ffc600;
  &:hover {
    animation: gritty 2s steps(10) infinite;
  }
}

.highlightWide {
  padding: 1rem;
  letter-spacing: -1px;
  background: url("/images/photos/blackgrit.png");
  background-color: #ffc600;
  &:hover {
    animation: gritty 2s steps(10) infinite;
  }
}



