.table {
  width: 100%;
  td {
    padding: 5px;
  }
  tr:nth-child(even) {
    background: white;
  }
  tr:nth-child(odd):not(:first-child) {
    background: #f7f7f7;
  }
  thead tr {
    background: $black;
    color: white;
  }
}

.operatorsTable {
  position: relative;
  width: 100%;
  border: 1px solid black;
  border-collapse: separate;
  text-align: center;
  td {
    padding: 5px;
    font-size: 1.6rem;
    border: 1px solid black;
  }
  thead tr {
    background: $black;
    color: white;
    font-size: 1.8rem;
    border: 1px solid black;
  }
  thead th {
    min-width: 10rem;
  }
}



